import React, { Suspense } from "react";
import {
  BrowserRouter as Router,
  Route,
  Redirect,
  Switch,
} from "react-router-dom";
import Header from "./Header/Header";
import MenuBar from "./MenuBar/MenuBar";
import Auth from "./Auth/Auth";
import Chats from "./Chats/Chats";
import Messenger from "./Messenger/Messenger";
import FriendList from "./Friends/FriendList";
import Posts from "./Posts/Posts";

import { AuthContext } from "./shared/Context/auth-context";
import { useAuth } from "./shared/components/hooks/auth-hook";
import LoadingSpinner from "./shared/components/UIElements/LoadingSpinner";

import "./App.css";

function App() {
  const { token, login, logout, userId } = useAuth();
  let routes;
  if (token) {
    routes = (
      <Switch>
        <Route path="/tango-insta_messenger/chats" exact>
          <MenuBar />
          <Chats />
        </Route>
        <Route path="/tango-insta_messenger/friends" exact>
          <MenuBar />
          <FriendList />
        </Route>
        <Route path="/tango-insta_messenger/posts" exact>
          <MenuBar />
          <Posts />
        </Route>
        <Route path="/tango-insta_messenger/chats/:userId" exact>
          <MenuBar />
          <Messenger />
        </Route>
        <Redirect to="/tango-insta_messenger/chats" />
      </Switch>
    );
  } else {
    routes = (
      <Switch>
        <Route path="/tango-insta_messenger" exact>
          <Auth />
        </Route>
        <Redirect to="/tango-insta_messenger" />
      </Switch>
    );
  }
  return (
    <AuthContext.Provider
      value={{
        isLoggedIn: !!token,
        token: token,
        userId: userId,
        login: login,
        logout: logout,
      }}
    >
      <Router>
        <Header />
        <Suspense
          fallback={
            <div className="center">
              <LoadingSpinner asOverlay />
            </div>
          }
        >
          {routes}
        </Suspense>
      </Router>
    </AuthContext.Provider>
  );
}

export default App;
