import FriendItem from "./FriendItem";

import React, { useState, useEffect, useContext } from "react";
import { AuthContext } from "../shared/Context/auth-context";
import { useHttpClient } from "../shared/components/hooks/http-hook";
import ErrorModal from "../shared/components/UIElements/ErrorModal";
import AddFriend from "./AddFriend";
import LoadingSpinner from "../shared/components/UIElements/LoadingSpinner";

import "./FriendItem.css";

export default function FriendList(props) {
  const auth = useContext(AuthContext);
  const { isLoading, error, sendRequest, clearError } = useHttpClient();
  const [friends, setFriends] = useState([]);
  const [reloadFriends, setReloadFriends] = useState(false);

  useEffect(() => {
    async function fetchData() {
      try {
        setFriends([]);
        const responseData = await sendRequest(
          `${process.env.REACT_APP_BACKEND_URL}/user/${auth.userId}`
        );
        setFriends(responseData.friends);
      } catch (err) {}
    }
    if (auth.userId) fetchData();
  }, [auth.userId, sendRequest, reloadFriends]);

  return (
    <div className="all-chat">
      <AddFriend reloadFetch={setReloadFriends} />
      <ErrorModal error={error} onClear={clearError} />
      {isLoading && (
        <div className="center">
          <LoadingSpinner />
        </div>
      )}
      {friends.length !== 0 &&
        friends.map((friendId, index) => (
          <FriendItem key={index} userId={friendId} />
        ))}
    </div>
  );
}
