import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import { useHttpClient } from "../shared/components/hooks/http-hook";
import ErrorModal from "../shared/components/UIElements/ErrorModal";

export default function FriendItem(props) {
  const [friend, setFriend] = useState();
  const { error, sendRequest, clearError } = useHttpClient();
  useEffect(() => {
    async function fetchFriend() {
      try {
        const responseData = await sendRequest(
          `${process.env.REACT_APP_BACKEND_URL}/user/${props.userId}`
        );
        setFriend({
          id: responseData.id,
          name: responseData.name,
        });
      } catch (err) {}
    }
    fetchFriend();
    // setPerson(people.filter((person) => person.id === props.userId)[0]);
  }, [props.userId, sendRequest]);
  return (
    <React.Fragment>
      {friend && (
        <div className="chat-box">
          <ErrorModal error={error} onClear={clearError} />
          <img
            className="friend-image"
            alt="friend-img"
            src="https://picsum.photos/200/200"
          />
          <Link
            to={`/tango-insta_messenger/chats/${friend.id}`}
            className="friend-info-a"
          >
            <div className="friend-info">
              <h5 className="friend-name">{friend.name}</h5>
            </div>
          </Link>
        </div>
      )}
    </React.Fragment>
  );
}
