import React, { useState, useEffect, useContext } from "react";
import { Link } from "react-router-dom";
import { useHttpClient } from "../shared/components/hooks/http-hook";
import { AuthContext } from "../shared/Context/auth-context";
import ErrorModal from "../shared/components/UIElements/ErrorModal";
import LoadingSpinner from "../shared/components/UIElements/LoadingSpinner";

export default function ChatBox(props) {
  const auth = useContext(AuthContext);
  const { isLoading, error, sendRequest, clearError } = useHttpClient();
  const [person, setPerson] = useState();
  useEffect(() => {
    async function fetch() {
      const messageData = await sendRequest(
        `${process.env.REACT_APP_BACKEND_URL}/message/${props.userId}`
      );
      const friendId = messageData.users.filter((item) => item !== auth.userId);
      const friendData = await sendRequest(
        `${process.env.REACT_APP_BACKEND_URL}/user/${friendId}`
      );
      setPerson({
        id: friendData.id,
        name: friendData.name,
       // lastMsg: friendData.lastMsg.message,
      });
    }

    fetch();
    //setPerson(people.filter((person) => person.id === props.userId)[0]);
  }, [auth.userId, sendRequest, props.userId]);
  return (
    <React.Fragment>
      <ErrorModal error={error} onClear={clearError} />
      {isLoading && <LoadingSpinner />}
      {person && (
        <div className="chat-box">
          <img
            className="friend-image"
            alt="friend-img"
            src="https://picsum.photos/200/200"
          />
          <Link
            to={`/tango-insta_messenger/chats/${person.id}`}
            className="friend-info-a"
          >
            <div className="friend-info">
              <h5 className="friend-name">Name : {person.name}</h5>
              <p className="friend-msg">Message: {person.lastMsg} </p>
            </div>
          </Link>
        </div>
      )}
    </React.Fragment>
  );
}
