import React from "react";
import { Link } from "react-router-dom";
import "./MenuBar.css";

export default function MenuBar() {
  return (
    <div className="menu-bar">
      <Link to="/tango-insta_messenger/chats">
        <div className="chats">
          <p>Chats</p>
        </div>
      </Link>
      <Link to="/tango-insta_messenger/friends">
        <div className="friends">
          <p>Friends</p>
        </div>
      </Link>
      <Link to="/tango-insta_messenger/posts">
        <div className="posts">
          <p>Posts</p>
        </div>
      </Link>
    </div>
  );
}
