function ChatRoom() {
  const messagesRef = firestore.collection("messages");
  const query = messagesRef.orderBy("userId").limit(25);

  const [messages] = useCollectionData(query, { idField: "id" });

  console.log(messages);
}

async function writeFire(userId, msg) {
  const messagesRef = firestore.collection("messages");
  await messagesRef.add({
    timeStamp: firebase.firestore.FieldValue.serverTimestamp(),
    userId: userId,
    message: msg,
  });
}

//console.log(msg);
export default function Messenger(props) {
  const auth = useContext(AuthContext);
  const friendId = useParams().userId;
  const [fireData, setFireData] = useState();
  const [input, setInput] = useState();
  const [chats, setChats] = useState([]);
  const [person, setPerson] = useState();
  const [msgId, setMsgId] = useState();
  const { error, sendRequest, clearError } = useHttpClient();
  // const [pusherdata, setpusher] = useState();
  //ChatRoom();
  useEffect(() => {
    async function fetchFire() {
      // const data = await firestore
      //   .collection("messages")
      //   .doc("J7vZV0ONpHkAb9OVvclH").get();
      const docRef = await firestore.collection("messages").doc(msgId);
      docRef.get().then((docSnap) => {
        if (docSnap.exists)
          docRef.onSnapshot((doc) => setChats(doc.data().messages));
        else {
          docRef.set({
            messages: [],
            users: [auth.userId, friendId],
          });
          docRef.onSnapshot((doc) => setChats(doc.data().messages));
        }
      });
      // docref
      //   .doc("J7vZV0ONpHkAb9OVvclH")
      //   .onSnapshot((doc) => setFireData(doc.data()));

      // docref.doc("newDoc").set({
      //   name: "newdoc",
      // });
      // setFireData(doc);
      // console.log(doc.data());
      //const query = messagesRef.orderBy("userId").limit(25);
      // data.docs.forEach((doc) => console.log(doc.data()));
      //setFireData(data.docs.map(doc => doc.data()));
      // setFireData(doc);
      // console.log(messagesRef.docs)
      //const [messages] = useCollectionData(query, { idField: "id" });
    }
    if (msgId && friendId) fetchFire();
  }, [auth.userId, msgId, friendId]);
  //fireData && console.log(fireData);
  //console.log(messages);
  //fireData && console.log(fireData);
  // console.log(pusherdata);

  // useEffect(() => {
  //   ChatRoom();
  // }, []);

  useEffect(() => {
    async function fetchMessages() {
      const messageData = await sendRequest(
        `${process.env.REACT_APP_BACKEND_URL}/message`,
        "POST",
        JSON.stringify({
          user: auth.userId,
          friend: friendId,
        }),
        {
          "Content-Type": "application/json",
        }
      );

      const friendData = await sendRequest(
        `${process.env.REACT_APP_BACKEND_URL}/user/${friendId}`
      );
      setPerson({
        name: friendData.name,
      });
      setMsgId(messageData._id);

      // setChats((prevValue) => prevValue.concat(messageData.messages));
    }
    fetchMessages();
  }, [auth.userId, friendId, sendRequest, input]);

  // function handleTextFieldInput(event) {
  //    setInput(event.target.value);
  // }

  async function handleTextFieldSubmit(event) {
    event.preventDefault();
    const inputData = event.target.inputMsg.value;
    await firestore
      .collection("messages")
      .doc(msgId)
      .set({
        messages: [
          ...chats,
          {
            time: "2021-04-01",
            sender: auth.userId,
            message: inputData,
          },
        ],
      });
    await sendRequest(
      `${process.env.REACT_APP_BACKEND_URL}/message/${msgId}`,
      "POST",
      JSON.stringify({
        time: "2021-04-01",
        sender: auth.userId,
        message: event.target.inputMsg.value,
      }),
      {
        "Content-Type": "application/json",
      }
    );

    event.target.inputMsg.value = "";
    // setInput("");
  }
  return (
    <div className="messenger-box">
      <ErrorModal error={error} clearError={clearError} />
      {/* {isLoading && <LoadingSpinner />} */}
      {person && (
        <header className="messenger-header">
          <Link to="/tango-insta_messenger/chats">
            <button type="backButton" onClick={props.onClick}>
              <p>‚Üê</p>
            </button>
          </Link>
          <img
            className="friend-image"
            alt="friend-img"
            src="https://picsum.photos/200/200"
          />
          <div>
            <h1>{person.name} </h1>
          </div>
        </header>
      )}
      <div className="messages">
        <div className="all-chats">
          {chats.length !== 0 &&
            chats.map((message, index) => {
              return (
                <div key={index}>
                  <p
                    className={
                      message.sender === auth.userId ? "right" : "left"
                    }
                  >
                    {message.message}
                  </p>
                </div>
              );
            })}
        </div>
        <div className="textfield">
          <span>
            <p>üòä</p>
          </span>
          <form className="textfield-form" onSubmit={handleTextFieldSubmit}>
            <input
              type="text"
              id="inputMsg"
              //onChange={handleTextFieldInput}
              //value={input}
            />
            <button type="submit" />
          </form>
          <span>
            <p>üìé</p>
          </span>
          <span>
            <p>üì∑</p>
          </span>
        </div>
      </div>
    </div>
  );
}
